body{
  background: rgb(54, 217, 182);
  background: linear-gradient(90deg, rgb(201, 168, 214), 0%, rgb(62, 3, 117) 43%);
}

h1, h2, h3, h4, h5, h6, p, span, strike{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#task_container{
  max-width: 800px;
  margin: 0 auto;
  box-shadow: 0 14px 28px rgba(50,0,0, 0.25) 0 10px 10px rgba(0,0,0,0.22);
  background-color: #fff;

  margin-top: 100px;
  margin-bottom: 100px;

  justify-content: space-around;
  align-items: flex-start;

}

#form_wrapper{
  position: -webkit-sticky;
  position: sticky;
  top: 0rem;
  border-bottom: 1px solid #e9e9e9;
  background-color: #fff;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.25);
  padding: 40px;
}

#submit{
  background-color: #36d9b6;
  border-radius: 0;
  border: 0;
  color: #fff;
}

.flex_wrapper{
  display: flex;
}

.task_wrapper{
  cursor: pointer;
  border-bottom: 1px solid #e9e9e9;
  color: #686868;
  margin: 5px;
  padding: 20px;

}

.task_wrapper :hover{
  background-color: #F0F0F0;
}